<template>
  <Modal title="Upload ISBN" v-model="isShowModal">
    <template #header>
      <div class="flex justify-between items-center">
        <div>
          <h2 class="text-xl font-semibold">Upload ISBN</h2>
        </div>
        <div>
          <Button
            @click="handleDownloadCSVTemplate"
            :loading="loading.downloadTemplateFile"
            color="teal"
            dense
          >
            <span><i class="material-icons mr-2">download</i>Template CSV</span>
          </Button>
        </div>
      </div>
    </template>
    <template #default>
      <div>
        <Alert color="blue" view="smooth" class="mb-5" dense>
          In the template, items priced at $0 wonâ€™t appear in the Widget after upload, and any blank price cells or formatting issues will trigger a validation error. Your upload will continue, and you will see a validation report listing any errors once it is complete.
          <template #left>
            <span class="material-icons text-4xl">info</span>
          </template>
        </Alert>
        <BookIsbnFileUpload
          :files="latestISBNUploadedFile"
          @completed="handleUploadCompleted"
          @error="handleUploadError"
          @uploading="handleUploading"
          @remove="handleRemovedFile"
        />
      </div>
    </template>
    <template #footer>
      <Button
        @click="handleProcessUploadedFile"
        :disabled="isDisabledCSVUpload"
        :loading="loading.processUploadedFile || isUploading"
        color="teal"
      >
        <span>
          <i class="material-icons mr-2">upload</i> Update Existing ISBNs</span
        >
      </Button>
    </template>
  </Modal>
</template>
<script>
import { defineComponent, ref, computed } from 'vue'
import { useISBN } from 'composable/product/book'
import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import BookIsbnFileUpload from './BookIsbnFileUpload.vue'
import Alert from 'bbUi/components/alert/alert.vue'

export default defineComponent({
  name: 'BookIsbnModalUpload',
  components: {
    Modal,
    Button,
    Alert,
    BookIsbnFileUpload
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'completed'],
  setup(props, { emit }) {
    const {
      state,
      loading,
      getLatestUploadedFile,
      deleteUploadedFile,
      downloadTemplateFile,
      processUploadedFile
    } = useISBN()
    const isUploadError = ref(false)
    const isUploading = ref(false)
    const isShowModal = computed({
      set: (isOpen) => emit('update:modelValue', isOpen),
      get: () => props.modelValue
    })
    const isDisabledCSVUpload = computed(
      () =>
        !state.latestUploadedFile.pricing_data_real_filename ||
        isUploadError.value
    )
    const latestISBNUploadedFile = computed(() => {
      return state.latestUploadedFile.pricing_data_real_filename
        ? [
            {
              name: state.latestUploadedFile.pricing_data_real_filename || '',
              size: state.latestUploadedFile.pricing_data_files_size || 0
            }
          ]
        : []
    })

    const handleDownloadCSVTemplate = async () => await downloadTemplateFile()
    const handleUploadCompleted = async () => {
      isUploadError.value = false
      await getLatestUploadedFile()
    }
    const handleUploadError = () => {
      isUploadError.value = true
    }
    const handleUploading = (isUploadingState = false) => {
      isUploading.value = isUploadingState
    }
    const handleRemovedFile = async (file) => {
      const isUploadedFile = file?.status
        ? file.status?.progress === 100 && file.status?.error === false
        : false
      if (file === null || isUploadedFile) {
        state.latestUploadedFile.pricing_data_real_filename = null
        await deleteUploadedFile()
        await getLatestUploadedFile()
        return
      }
      isUploadError.value = false
    }
    const handleProcessUploadedFile = async () => {
      await processUploadedFile()
      emit('completed', true)
    }
    return {
      state,
      loading,
      isShowModal,
      isDisabledCSVUpload,
      isUploading,
      latestISBNUploadedFile,
      handleDownloadCSVTemplate,
      handleUploadCompleted,
      handleUploadError,
      handleUploading,
      handleRemovedFile,
      handleProcessUploadedFile
    }
  }
})
</script>
