<template>
  <Modal title="Upload ISBN" v-model="isShowModal" hide-footer size="md">
    <template #header>
      <div class="flex justify-between items-center">
        <div>
          <h2 class="text-xl font-semibold">Invalid Format Data</h2>
        </div>
        <div>
          <Button
            @click="handleDownloadInvalidCSV"
            :loading="loading.getInvalidData"
            color="teal"
            dense
          >
            <span><i class="material-icons mr-2">download</i>CSV</span>
          </Button>
        </div>
      </div>
    </template>
    <template #default>
      <div v-loading="loading.getInvalidData">
        <Alert class="mb-4" color="red" view="smooth" dense>
          <template #default>
            <p class="my-4">
              We identified <b>{{ state.invalidData.total }}</b> rows of data
              that don't meet the specified format requirements.
            </p>
            <template v-if="state.invalidData.total > 99">
              <p class="my-4">
                To assist you, you can download a CSV file containing only the data that does not meet the required format. This file will help you identify and correct the formatting issues.
              </p>
              <Button
                @click="handleDownloadInvalidCSV"
                class="mb-4"
                color="teal"
                view="outlined"
                dense
              >
                <i class="material-icons">download</i> CSV</Button
              >
            </template>
          </template>
          <template #left>
            <span class="material-icons mt-3">warning</span>
          </template>
        </Alert>
        <div class="text-right my-6">
          <p>
            Last Update:
            <b>{{
              state.invalidData.lastUpdatedAt
                ? moment(
                    state.invalidData.lastUpdatedAt,
                    'YYYY-MM-DD HH:mm:ss'
                  ).format(me.datetime_format)
                : ''
            }}</b>
          </p>
        </div>
        <div
          v-if="
            state.invalidData.total < 99 || state.invalidData.rows.length > 0
          "
          class="p-4 border border-grey-01 rounded-lg mb-4"
        >
          <Table :data="state.invalidData.rows" height="500">
            <TableColumn prop="ISBN" label="ISBN" />
            <TableColumn prop="Title" label="Title" width="250" />
            <TableColumn prop="Bid" label="Bid" />
            <TableColumn prop="PUBLISHER" label="Publisher" />
            <TableColumn prop="Reason" label="Reason" />
          </Table>
        </div>
      </div>
    </template>
  </Modal>
</template>
<script>
import { defineComponent, computed, watch } from 'vue'
import { useISBN } from 'composable/product/book'
import { useUser } from 'composable/user'
import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import Alert from 'bbUi/components/alert/alert.vue'

import moment from 'moment'

export default defineComponent({
  name: 'BookIsbnModalUpload',
  components: {
    Modal,
    Button,
    Alert
  },
  props: {
    modelValue: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const { state, loading, getInvalidData } = useISBN()
    const { me } = useUser()
    const isShowModal = computed({
      set: (isOpen) => emit('update:modelValue', isOpen),
      get: () => props.modelValue
    })

    const handleDownloadInvalidCSV = () => {
      window.open(state.invalidData.fileUrl, '_blank', 'noreferrer noopener')
    }
    watch(() => isShowModal.value, async (isShow) => {
      if(isShow) await getInvalidData()
    })

    return {
      state,
      loading,
      isShowModal,
      me,
      moment,
      handleDownloadInvalidCSV
    }
  }
})
</script>
