<template>
  <div>
    <FileUpload
      :accept="['csv']"
      :config="fileUploadConfig"
      :files="files"
      single-upload
      @completed="handleCompleteUpload"
      @error="handleErrorUpload"
      @uploading="(isUploading) => $emit('uploading', isUploading)"
      @remove="handleRemovedFile"
    >
      <template #types="{ acceptedFileTypes }">
        <small class="block mt-4">
          <span class="font-semibold">File:</span> {{ acceptedFileTypes }}
          <span class="font-semibold">Format:</span> ISBN, Title, Bid, Publisher
        </small>
      </template>
      <template #file-list="{ files: fileList, removeFile, isEmptyFiles }">
        <div>
          <div v-if="isEmptyFiles && files?.length > 0">
            <div
              v-for="file in files"
              :key="file.name"
              :class="['file-list__item']"
            >
              <div class="file-list__file-icon pr-6">
                <i class="material-icons">insert_drive_file</i>
              </div>
              <div class="relative w-full">
                <div class="flex items-center">
                  <div class="file-list__file-name">{{ file.name }}</div>
                  <div class="file-list__file-size">
                    {{ fileSizeFormat(file.size) }}
                  </div>
                </div>
              </div>
              <div class="file-list__file-remove pl-6">
                <a @click.prevent="handleRemovedFile(null)"
                  ><i class="material-icons">cancel</i></a
                >
              </div>
            </div>
          </div>
          <div v-else-if="!isEmptyFiles" class="px-4 py-4">
            <div
              v-for="file in fileList"
              :key="file.name"
              :class="[
                'file-list__item',
                file.status.progress < 100 ? 'file-list__item--onprogress' : ''
              ]"
            >
              <div class="file-list__file-icon pr-6">
                <i class="material-icons">insert_drive_file</i>
              </div>
              <div class="relative w-full">
                <div class="flex items-center">
                  <div class="file-list__file-name">{{ file.name }}</div>
                  <div class="file-list__file-size">{{ file.size }}</div>
                </div>
                <transition name="fade">
                  <div
                    v-if="file.status.progress < 100"
                    class="file-list__progress-bar"
                    :style="{ width: `${file.status.progress}%` }"
                  ></div
                ></transition>
              </div>
              <div class="file-list__file-remove pl-6">
                <a @click.prevent="removeFile(file.id)"
                  ><i class="material-icons">cancel</i></a
                >
              </div>
            </div>
          </div>
        </div>
      </template>
    </FileUpload>
  </div>
</template>
<script>
import { defineComponent, reactive } from 'vue'
import FileUpload from 'components/FileUpload.vue'
import { fileSizeFormat } from 'utils/number'

export default defineComponent({
  name: 'BookIsbnFileUpload',
  components: {
    FileUpload
  },
  props: {
    files: {
      type: Array,
      default: () => []
    },
    loading: {
      type: Boolean,
      default: false
    }
  },
  emits: ['completed', 'error', 'uploading', 'remove'],
  setup(props, { emit }) {
    const fileUploadConfig = reactive({
      url: '/v1/pos/pricing/upload-pricing',
      method: 'post',
      field: 'csv'
    })
    const handleCompleteUpload = (files = []) => {
      emit('completed', files)
    }
    const handleErrorUpload = (errors = []) => {
      emit('error', errors)
    }
    const handleRemovedFile = (file = null) => {
      emit('remove', file)
    }
    return {
      fileUploadConfig,
      handleCompleteUpload,
      handleErrorUpload,
      handleRemovedFile,
      fileSizeFormat
    }
  }
})
</script>
