<template>
  <div>
    <div class="flex justify-between items-center mb-8">
      <!-- <div class="lg:flex lg:divide-x lg:divide-grey-01">
        <div class="flex lg:pr-4 lg:mb-0 mb-4">
          <router-link :to="{ name: 'setting' }" class="text-dark mr-4"><span class="material-icons">chevron_left</span></router-link>
          <h5>Catalog</h5>
        </div>
        <div class="lg:pl-4">
          <Breadcrumb :items="breadcrumb">
            <span class="material-icons">chevron_right</span>
          </Breadcrumb>
        </div>
      </div> -->
      <Breadcrumb
        pageTitle="Catalog"
        :backTo="{ name: 'setting' }"
        :items="breadcrumb"
      />
    </div>
    <Card>
      <router-view />
      <BookISBNModal
        v-model="isShowISBNUpdateModal"
        @completed="handleISBNProcessing"
        style="z-index: 2000"
      />
      <BookISBNInvalidModal
        v-model="isShowISBNInvalidModal"
        style="z-index: 2000"
      />
    </Card>
    <Card class="mt-4" whitelabel="remove">
      Can't find an item? Let us know and we will add it to our catalog.
      <Button class="ml-2" color="teal" @click="handleRequestDevice" dense>Request Item</Button>
    </Card>
  </div>
</template>
<script>
import { computed, defineComponent } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useISBN } from 'composable/product/book'
import { useNotification } from 'composable/notification'
import { useBuybackDevice } from 'composable/setting'

import Card from 'bbUi/components/card/card.vue'
import Button from 'bbUi/components/button/button.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import BookISBNModal from 'components/BuybackDevice/Book/BookIsbnModal.vue'
import BookISBNInvalidModal from 'components/BuybackDevice/Book/BookIsbnInvalidModal.vue'

export default defineComponent({
  components: {
    Card,
    Button,
    Breadcrumb,
    BookISBNModal,
    BookISBNInvalidModal
  },
  async setup() {
    const route = useRoute()
    const router = useRouter()
    const { actionCategories: fetchProductCategories } = useBuybackDevice()
    const breadcrumb = [
      {
        text: 'Settings',
        to: {
          name: 'setting'
        }
      },
      {
        text: 'Catalog'
      }
    ]
    const { state: stateISBN, getLatestUploadedFile } = useISBN()
    const { openNotification } = useNotification()

    const isShowISBNUpdateModal = computed({
      get: () => {
        return (
          route.params?.category?.toLowerCase?.() === 'books' &&
          route.query.isbn === 'update'
        )
      },
      set: (value) => {
        if (value === false) router.push({ ...route, query: {} })
      }
    })
    const isShowISBNInvalidModal = computed({
      get: () => {
        return (
          route.params?.category?.toLowerCase?.() === 'books' &&
          route.query.isbn === 'invalid'
        )
      },
      set: (value) => {
        if (value === false) router.push({ ...route, query: {} })
      }
    })

    const handleRequestDevice = () => {
      window.open(
        'https://forms.clickup.com/8427959/f/816dq-50276/FKAAVUJSUQ7G44BNOD',
        '_blank',
        'noreferrer noopener'
      )
    }
    const handleISBNProcessing = async () => {
      await router.push({
        name: 'settingBuybackDeviceTab',
        params: {
          category: 'books'
        },
        query: {}
      })
      await fetchProductCategories()
      openNotification('NotificationPricingBooks')
    }

    await getLatestUploadedFile()

    return {
      stateISBN,
      isShowISBNUpdateModal,
      isShowISBNInvalidModal,
      breadcrumb,
      handleRequestDevice,
      handleISBNProcessing
    }
  }
})
</script>
